import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../services/api.service';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css']
})
export class ContactsComponent implements OnInit {
  contacts: any[] = [];
  newName = '';
  newPhone = '';
  newPlate = '';
  bulkText = '';
  message = '';
  messageColor = '';

  constructor(private api: ApiService) {}

  ngOnInit() {
    this.loadContacts();
  }

  loadContacts() {
    this.api.getContacts().subscribe({
      next: (res) => (this.contacts = res),
      error: () => this.showMessage('‚ùå Error al cargar contactos', 'red')
    });
  }

  createContact() {
    const nuevo = {
      nombre: this.newName,
      celular: this.newPhone,
      placa: this.newPlate
    };

    if (!nuevo.nombre || !nuevo.celular || !nuevo.placa) {
      this.showMessage('‚ö†Ô∏è Completa todos los campos', 'orange');
      return;
    }

    this.api.createContact(nuevo).subscribe({
      next: () => {
        this.contacts.push(nuevo); // üëà agrega el nuevo contacto a la lista
        this.newName = '';
        this.newPhone = '';
        this.newPlate = '';
        this.showMessage('‚úÖ Contacto creado correctamente', 'green');
      },
      error: () => {
        this.showMessage('‚ùå Error al crear el contacto', 'red');
      }
    });
  }

  uploadBulk() {
    try {
      const data = JSON.parse(this.bulkText);
      const rows = data.rows || data.filas || [];
      if (rows.length === 0) {
        this.showMessage('‚ö†Ô∏è No se encontraron filas', 'orange');
        return;
      }

      this.api.bulkContacts(rows).subscribe({
        next: () => {
          // üëá Agrega los nuevos contactos visualmente
          this.contacts = [...this.contacts, ...rows];
          this.showMessage('‚úÖ Carga masiva exitosa', 'green');
        },
        error: () => this.showMessage('‚ùå Error en la carga masiva', 'red')
      });
    } catch {
      this.showMessage('‚ùå JSON inv√°lido', 'red');
    }
  }

  showMessage(msg: string, color: string) {
    this.message = msg;
    this.messageColor = color;
    setTimeout(() => {
      this.message = '';
    }, 3000);
  }
}
