import { Component } from '@angular/core';
import { ApiService } from '../../services/api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {
  email = '';
  password = '';
  message = '';
  messageColor = '';

  constructor(private api: ApiService, private router: Router) {}

  login() {
    console.log('üëâ Ejecutando login con:', this.email, this.password);

    this.api.login(this.email, this.password).subscribe({
      next: (res) => {
        console.log('‚úÖ Login exitoso:', res);
        this.api.setToken(res.token);
        this.message = '‚úÖ Login exitoso. Redirigiendo...';
        this.messageColor = 'green';

        // redirige despu√©s de 1 segundo
        setTimeout(() => this.router.navigate(['/contacts']), 1000);
      },
      error: (err) => {
        console.error('‚ùå Error en login:', err);
        this.message = '‚ùå Credenciales incorrectas';
        this.messageColor = 'red';
      }
    });
  }
}