import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class ApiService {
  private baseUrl = 'https://api.larueda.com.co/api/testingreso';
  private candidateCode = 'CAND_0011';
  private token: string = '';

  constructor(private http: HttpClient) {}

  setToken(token: string) {
    this.token = token;
  }

  // ====================================================
  // M√âTODO DE LOGIN SIMULADO
  // ====================================================
  login(email: string, password: string): Observable<any> {
    console.log('Intentando login con:', email, password); // üëà para debug

    return new Observable((observer) => {
      if (email.trim() === 'cand_0011@larueda.com' && password.trim() === 'Password123') {
        console.log('‚úÖ Credenciales correctas');
        observer.next({
          token: 'token_simulado_123',
          user: { email, candidate_code: this.candidateCode }
        });
      } else {
        console.log('‚ùå Credenciales incorrectas');
        observer.error({
          status: 'error',
          code: 400,
          message: 'Login incorrecto'
        });
      }
      observer.complete();
    });
  }

  // ====================================================
  // M√âTODOS SIMULADOS PARA CONTACTOS
  // ====================================================
  getContacts(): Observable<any> {
    return new Observable((observer) => {
      observer.next([
        { nombre: 'Juan P√©rez', celular: '3001112233', placa: 'ABC123' },
        { nombre: 'Ana Ruiz', celular: '3002223344', placa: 'XYZ987' }
      ]);
      observer.complete();
    });
  }

  createContact(data: any): Observable<any> {
    console.log('Simulado: creando contacto', data);
    return new Observable((observer) => {
      observer.next({ status: 'ok' });
      observer.complete();
    });
  }

  bulkContacts(rows: any[]): Observable<any> {
    console.log('Simulado: carga masiva', rows);
    return new Observable((observer) => {
      observer.next({ status: 'ok' });
      observer.complete();
    });
  }
}
